import {React, useEffect, useState, useRef, useContext} from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import API from '../../../../Services';
import { ContextDados }  from '../../../../contexts/Data';
import PageWrapper from '../../../../layout/PageWrapper/PageWrapper';
import Page from '../../../../layout/Page/Page';
import 	Button, {ButtonGroup} from '../../../../components/bootstrap/Button';
import Modal, {ModalBody,ModalFooter,ModalHeader,ModalTitle} from '../../../../components/bootstrap/Modal';
import { cirurgicalFlow } from '../../../../menu';
import Header from '../../../../components/Header';
import  {DropdownItem, DropdownMenu, DropdownToggle} from '../../../../components/bootstrap/Dropdown';
import Dropdown from '../../../../components/bootstrap/Dropdown';
import "./style.css"



const Listagem = () => {

const navigate = useNavigate()
const location = useLocation()


/**
 * Variáveis de Estado
 */

const [patients, 			setPatients] 					= useState(	[]	)
const [Loading, 			setLoading] 					= useState(true)
const [CodigoLocalidade, 	setCodigoLocalidade		] 		= useState(null	)
const [CodigoSubLocalidade, setCodigoSubLocalidade	] 		= useState(null	)
const [time,            	setTime					] 		= useState(Date.now())
const [mostraBotao, 		setMostraBotao	] 				= useState(false)

/**
 * @Context API
 */

 const { setInfoPaciente } = useContext(ContextDados) 
  

/**
 * Controlador do Botão
 */

const [buttonStatusAptos, 	setButtonStatusAptos	] 		= useState(false)
const [buttonStatusTodos, 	setButtonStatusTodos	] 		= useState(false)
const [DisableButtonAptos, 	setDisableButtonAptos	] 		= useState(false)
const [DisableButtonTodos, 	setDisableButtonTodos	] 		= useState(false)

// eslint-disable-next-line no-unused-vars
const [ModalCirurgiaCancelada, 	setModalCirurgiaCancelada] 	= useState(false)



const desabilitarSelecao = useRef(null)


useEffect(() => {
/*
	ListarPacientes			(2,location.state.CODIGO_LOCALIDADE	)
*/
	setCodigoLocalidade		(location.state.CODIGO_LOCALIDADE	)
	setCodigoSubLocalidade	(location.state.CODIGO_SUBLOCALIDADE)
	setMostraBotao (true)


const interval = setInterval(() => {
	setTime(Date.now())
	setMostraBotao(false)
}, 60000);
		



return () => {
	ListarPacientes(2)

		clearInterval(interval);

};


}, [location, navigate, CodigoSubLocalidade, time,  desabilitarSelecao]);


const refreshFiltro = (param) => {

	if(param==='transferir'){
		setPatients([])
		ListarPacientes(1)
		setDisableButtonAptos(true)		
		setButtonStatusAptos(false)
	}
	
	if(param==='receber'){
		setPatients([])
		ListarPacientes(2)
		setDisableButtonAptos(false)
		setDisableButtonTodos(true)			
	}
}



const ListarPacientes = async (paramStatus) => {
	setMostraBotao(true)
	if(paramStatus === 1)
	{
		try {	
			setLoading(true)		
			const patient = await API.get(`/PacientesAReceberPreparo?TIPO=t`);
			setPatients(patient.data)

			setDisableButtonAptos	(false)
			setButtonStatusAptos	(false)
			setButtonStatusTodos	(true)
			
		} catch (error) {
			console.log(error)
		}
		setLoading(false)
	}
	else if(paramStatus === 2)
	{

		try {		
			setLoading(true)	
			const patient = await API.get(`/PacientesAReceberPreparo?TIPO=r`);
			setPatients(patient.data)
			
			setButtonStatusAptos	(true)
			setButtonStatusTodos	(false)
			setDisableButtonTodos	(false)

		} catch (error) {
			console.log(error)
		}
		setLoading(false)
	}
	
};



const selecionarPaciente = async(infoPaciente) => {
	/**
	 * Realizará nova consulta para recuperar dados da próxima etapa.
	 */
	/*
	console.log(infoPaciente)
	*/
	try {
		const RecuperaDadosProximaEtapa = await API.get(`/retornaProximaEtapa?cd_localidade=${CodigoLocalidade}&cod_aviso=${infoPaciente.COD_AVISO}`)
		if(RecuperaDadosProximaEtapa)
		{

			setInfoPaciente(
				{
					CODIGO_LOCALIDADE: CodigoLocalidade,
					CODIGO_SUB_LOCALIDADE: CodigoSubLocalidade,
					DADOS: infoPaciente,
					INFO_PROX_ETAPA: {
						PROX_ETAPA: { 
							CD_LOCALIDADE: 1,
							CD_ETAPA: infoPaciente.CD_ETAPA,
							COD_AVISO: infoPaciente.COD_AVISO,
							PROX_ETAPA: infoPaciente.PROX_ETAPA
						} 
					}
				}
			)

			navigate('/salaPreparo/recebimento', {
				state: {
					CODIGO_LOCALIDADE: CodigoLocalidade,
					CODIGO_SUB_LOCALIDADE: CodigoSubLocalidade,
					DADOS: infoPaciente,
					INFO_PROX_ETAPA: {
						PROX_ETAPA: { 
							CD_LOCALIDADE: 1,
							CD_ETAPA: infoPaciente.CD_ETAPA,
							COD_AVISO: infoPaciente.COD_AVISO,
							PROX_ETAPA: infoPaciente.PROX_ETAPA
						} 
					}
				}
		
			  });
		}
	} catch (error) {
		console.log(error)
	}

	
};



/*
* Caso seja clicado no MENU na barra superior, irá quebrar o LocalStorage e retornará a página inicial.
*/

const alterarLocalidade = () => {

	localStorage.removeItem('CAPP-local');
	navigate('/')

};


	return (
		
		<PageWrapper title={cirurgicalFlow.PREPARO_LISTAGEM.text}>
		
			<Header titulo='Sala de Preparo' localidade='Lista de Pacientes'>
				<Dropdown >
					<DropdownToggle>
						<Button
							color="light"
							icon="menu"
							isLight
						>
							<p> </p>
						</Button>
					</DropdownToggle>
					<DropdownMenu>
						<DropdownItem onClick={() => alterarLocalidade()}>
							Alterar Local
						</DropdownItem>
					</DropdownMenu>
				</Dropdown>
			</Header>
			<Page>
			
			<div className='position-filter'>
					{
						mostraBotao  &&
						<>
							<Button size='lg'  rounded={3} disabled={DisableButtonAptos} color="info" isActive={buttonStatusAptos} isLight onClick={()=> refreshFiltro('receber')}>
								Receber
							</Button>

							<Button size='lg' id="btnTodos"  rounded={3} disabled={DisableButtonTodos} style={{"margin": "3px"}} color="info" isActive={buttonStatusTodos}  isLight onClick={()=> refreshFiltro('transferir')}>
								Transferir
							</Button>
						</>
					}
				
				
				</div>
				<br />
			
				<div>
					
				{
					// eslint-disable-next-line no-nested-ternary
					Loading  ?
						<div className="lds-ring"><div /><div /><div /><div /></div>
					:
					// eslint-disable-next-line no-nested-ternary
					patients.length ?	
						patients.map((i) => {
							return (
								// eslint-disable-next-line jsx-a11y/no-static-element-interactions
								<div key={i.CD_AVISO} className={ i.CANCELADO == null ? 'bloco-paciente' : 'bloco-paciente-cancelado' }  onClick={ i.CANCELADO == null ? ()=>selecionarPaciente(i) : ()=>setModalCirurgiaCancelada(true)} onKeyDown={()=>selecionarPaciente(i)}>
								<div className='fw-bold'>
										{i.PACIENTE}
									</div>											
									<div className='text-muted'>
										<small style={{"color": i.CANCELADO != null && `#fff`}}>{i.NM_MAE}</small>
									</div>
									<div className='data_nasc'>
										{i.DT_NASCIMENTO}
										{ i.CANCELADO != null && <input className="btn-info-susp-cir" type="button" value="SUSPENSA"/>}
										
									</div>
								</div>
								
							
							);
						})
						:
						<div className="d-flex justify-content-center">
							<br /><br />
							<div className='lista-vazia'>
								<h1>Não há paciente!</h1>
							</div>
							
						</div>

              		 
               }
			   </div>

			   <Modal
					id='modalCancelamentoCirurgia'
					isOpen={ModalCirurgiaCancelada}
					titleId='modalCancelamentoCirurgia'>
						<ModalHeader style={{"backgroundColor":"#E5684C"}}>
							<ModalTitle style={{"color": "#fff"}} id='example-title'> 
								ALERTA
							</ModalTitle>
						</ModalHeader>
						<ModalBody className='ModalBody'>
							<h1>Esta cirurgia foi suspensa!</h1>
						</ModalBody>
						<ModalFooter>
							<ButtonGroup>
									<Button onClick={()=> setModalCirurgiaCancelada(false)} className='btn-style' color='light'>
										Fechar
									</Button>
							</ButtonGroup>
						</ModalFooter>
				</Modal>	

			</Page>
		</PageWrapper>
	);

	
};

export default Listagem;
